import { NextResponse } from 'next/server';
import { ZodError } from 'zod';

// Custom error classes
export class ApiError extends Error {
  constructor(
    public statusCode: number,
    message: string,
    public code?: string
  ) {
    super(message);
    this.name = 'ApiError';
  }
}

export class ValidationError extends ApiError {
  constructor(message: string, public details?: any) {
    super(400, message, 'VALIDATION_ERROR');
  }
}

export class UnauthorizedError extends ApiError {
  constructor(message: string = 'Unauthorized') {
    super(401, message, 'UNAUTHORIZED');
  }
}

export class ForbiddenError extends ApiError {
  constructor(message: string = 'Forbidden') {
    super(403, message, 'FORBIDDEN');
  }
}

export class NotFoundError extends ApiError {
  constructor(message: string = 'Not found') {
    super(404, message, 'NOT_FOUND');
  }
}

export class RateLimitError extends ApiError {
  constructor(message: string = 'Rate limit exceeded', public retryAfter?: number) {
    super(429, message, 'RATE_LIMIT');
  }
}

export class InternalError extends ApiError {
  constructor(message: string = 'Internal server error') {
    super(500, message, 'INTERNAL_ERROR');
  }
}

// Common error constructors
export const errors = {
  unauthorized: (message = 'Unauthorized') => new UnauthorizedError(message),
  forbidden: (message = 'Forbidden') => new ForbiddenError(message),
  notFound: (message = 'Not found') => new NotFoundError(message),
  badRequest: (message: string) => new ValidationError(message),
  internal: (message = 'Internal server error') => new InternalError(message),
  rateLimit: (message = 'Rate limit exceeded', retryAfter?: number) =>
    new RateLimitError(message, retryAfter),
};

// Error handler function
export function handleApiError(error: unknown): NextResponse {
  // Handle custom API errors
  if (error instanceof ApiError) {
    const response: any = {
      error: error.code,
      message: error.message,
    };

    if (error instanceof ValidationError && error.details) {
      response.details = error.details;
    }

    if (error instanceof RateLimitError && error.retryAfter) {
      return NextResponse.json(response, {
        status: error.statusCode,
        headers: {
          'Retry-After': error.retryAfter.toString(),
        },
      });
    }

    return NextResponse.json(response, { status: error.statusCode });
  }

  // Handle Zod validation errors
  if (error instanceof ZodError) {
    return NextResponse.json(
      {
        error: 'VALIDATION_ERROR',
        message: 'Invalid request data',
        details: error.issues,
      },
      { status: 400 }
    );
  }

  // Handle unknown errors
  console.error('Unhandled error:', error);

  return NextResponse.json(
    {
      error: 'INTERNAL_ERROR',
      message: 'An unexpected error occurred',
    },
    { status: 500 }
  );
}

// Async error wrapper for API routes
export function withErrorHandling(
  handler: (req: Request, context?: any) => Promise<NextResponse>
) {
  return async (req: Request, context?: any): Promise<NextResponse> => {
    try {
      return await handler(req, context);
    } catch (error) {
      return handleApiError(error);
    }
  };
}
